import uniSegmentedControl from "../../components/uni-segmented-control/uni-segmented-control.vue";
import validCode from "../../components/p-validCode/validCode.vue";
export default {
	components: {
		uniSegmentedControl,
		validCode
	},
	data() {
		return {
			imgHeight: "",
			current: 0,
			items: ['燃油汽车', '新能源汽车'],
			plateNumber: "",
			carType: ['客车', '出租车', '公交车', '包车', '旅游'],
			carType1: '',
			selector: '请选择 >',
		}
	},
	onLoad() {

	},
	methods: {
		//选择车类型
		selectorChange: function(e) {
			this.selector = this.carType[e.target.value];
			this.carType1 = this.carType[e.target.value];
		},
		async load() {
			var that = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					that.imgHeight = res.windowHeight;
				}
			});
		},
		goBack: function() {
			uni.clearStorageSync('CarType');
			uni.navigateBack();
		},
		onClickItem(e) { //tab点击事件
			var that = this;
			if (that.current !== e.currentIndex) {
				that.current = e.currentIndex;
			}
			that.$refs.code.clear();
		},
		getCode(val) {
			this.plateNumber = val;
		},
		Confirm(e) {
				var that = this;
				const {
					plateNumber,carType1
				} = this;
				var plate = this.plateNumber;
				var cartype1 = this.carType1;
				console.log(cartype1);
				if (this.current == 0) {
					if((plate.length ==7) && (cartype1 != null && cartype1 != "")){
						uni.setStorage({
							key: 'CarType',
							data: that.carType1,
							success() {
								uni.navigateTo({
									url: '/pages/driver/taxiDriver',
								})
							}
						})
					} else {
						uni.showToast({
							title: '请输入车牌号和选择车类型',
							icon: "none"
						})
		
					}
				}
				if (this.current == 1) {
					if (plate.length ==8) {
						uni.setStorage({
							key: 'CarType',
							data: that.carType,
							success() {
								uni.navigateTo({
									url: '/pages/driver/taxiDriver',
								})
							}
						})
					} else {
						uni.showToast({
							title: '请输入车牌号',
							icon: "none"
						})
				
					}
				}
			},
		}
}
